@import url('https://fonts.googleapis.com/css2?family=Oleo+Script+Swash+Caps&display=swap');

*{
  margin: 0;
  padding: 0;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  background-color: #f6f5f5;
}

.main{
  padding: 10px;
  max-width: 1024px;
  margin: 0 auto;
}

.scan-area{
  width: 300px;
  height: 300px;
  background-color: white;
  margin: 10px auto;
  position: relative;
  padding: 15px;
  border-radius: 8px;
  box-shadow:3px 3px 13px rgba(0,0,0, 0.2);
  box-sizing: border-box;
}

  .scan-area .laser {
    width: 270px;
    margin: 0 auto;
    background-color: tomato;
    height: 1.5px;
    position: absolute;
    top: 13px;
    z-index: 2;
    box-shadow: 0 0 4px rgb(253, 211, 22);
    animation: scanning 3s infinite;
  }
  .scan-area .diode {
    animation: beam 0.01s infinite;
  }
.header{
  display: flex;
  padding: 10px;
  justify-content: center;
  align-items: center;
}
  .header img{
    width: 45px;
  }
  .header h1{
    color: rgba(219, 39, 119, 1);
    text-align: center;
    font-size: 30px;
    font-family: 'Oleo Script Swash Caps', cursive;
  }

.title{
  background-color: #fff;
  margin: 20px auto;
  padding: 10px;
  width: 300px;
  border-radius: 8px;
  box-shadow:3px 3px 13px rgba(0,0,0, 0.2);
  box-sizing: border-box;
}

  .title h3{
    text-align: center;
    font-size:17px;
    color: rgb(56, 56, 56);
  }


 @keyframes beam {
	 50% {
		 opacity: 0;
	}
}
 @keyframes scanning {
	 50% {
		 transform: translateY(270px);
	}
}